<template>
  <div>
    <div class="more">
      <Panel>
        <template #header>
          <router-link to="/">首页</router-link>
          <span>/</span>
          <router-link :to="`/user/${$route.params.user}`"
            >{{ $route.params.user }}的主页</router-link
          >
        </template>
      </Panel>
      <Panel>
        <template #header> {{ $route.params.user }} 创建的话题 </template>
        <template #content v-if="user">
          <PostListItem
            v-for="item in user.recent_topics"
            :key="item.id"
            :list="item"
          >
          </PostListItem>
        </template>
      </Panel>
    </div>
  </div>
</template>

<script>
import { mapActions, mapState } from "vuex";
import PostListItem from "../components/postListItem.vue";
import Panel from "../containers/Panel.vue";
export default {
  components: { Panel, PostListItem },
  created() {
    // console.log(this.$route);
    this.getUserInfo(this.$route.params.user)
  },
  computed: {
    ...mapState({
      user: (state) => state.user.userInfo,
    }),
  },
  methods: {
    ...mapActions(["getUserInfo"]),
  },
};
</script>

