<template>
  <div class="postitem">
    <span
      :class="['post-tag', { active: list.good || list.top }]"
      v-if="isHasTag"
      >{{ tagText }}</span
    >

    <div class="post-title">
      <router-link :to="{ name: 'titlepage', params: { id: list.id } }">{{
        list.title
      }}</router-link>
      <!-- <router-link to=`/topic/${id}`>{{ list.title }}</router-link> -->
    </div>

    <div class="list-foot">
      <div>
        <router-link :to="`/user/${list.author.loginname}`">
          <el-avatar
            class="post-avatar"
            shape="square"
            size="medium"
            :src="list.author.avatar_url"
          ></el-avatar>
        </router-link>
        <span class="last-reply-time">{{
          list.last_reply_at | moment("from", "now")
        }}</span>
      </div>

      <div v-if="list.reply_count" class="count">
        <div>
          <span class="higtspan">{{ list.reply_count }}</span
          >/<span>{{ list.visit_count }}</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Vue from "vue";
export default {
  props: ["list"],
  // created() {
  //   console.log(this.list);
  // },
  computed: {
    isHasTag() {
      const page = this.$route.path;
      const isAllOrGoodPage =
        page === "/" || page === "/all" || page === "/good";
      return this.list.top ? true : isAllOrGoodPage ? true : false;
    },
    tagText() {
      return this.list.top
        ? "置顶"
        : this.list.good
        ? "精华"
        : Vue.filter("transformTab")(this.list.tab);
    },
  },
};
</script>

<style lang='less'>
.postitem {
  border-bottom: 1px solid #ccc;
  padding: 20px;
  margin-bottom: 20px;
  .post-avatar,
  .count,
  .post-tag,
  .last-reply-time {
    flex-shrink: 0;
  }

  .post-tag {
    margin-right: 10px;
    font-size: 12px;
    display: inline-block;
    padding: 2px 4px;
    background-color: #e5e5e5;
    color: #999;
    border-radius: 3px;
    &.active {
      color: #fff;
      background-color: #80bd01;
    }
  }
  .list-foot {
    margin-top: 20px;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    .post-avatar {
      width: 30px;
      height: 30px;
      margin-right: 10px;
    }
    .count {
      font-size: 12px;
      color: #ccc;
      margin-right: 10px;
      display: inline-block;
      text-align: right;
      margin-top: 20px;
      .higtspan {
        color: green;
        font-size: 14px;
      }
    }
    .last-reply-time {
      font-size: 12px;
    }
  }
  .post-title {
    // white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;

    flex-grow: 1;
    a {
      color: #333;
      font-size: 14px;
      &:visited {
        color: #ccc;
      }
      &:hover {
        text-decoration: underline;
      }
    }
  }
}
</style>